import React, { useState, useEffect } from 'react'
import './App.css'
import { Table, Tag, Input, Button, Space, BackTop, Tooltip, Row, Col } from 'antd'
import Highlighter from 'react-highlight-words'
import { SearchOutlined, DownloadOutlined } from '@ant-design/icons'

function swapArr(arr, index1, index2) {
  arr[index1] = arr.splice(index2, 1, arr[index1])[0]
  return arr
}

// 检索
let searchState = {
  searchText: '',
  searchedColumn: ''
}

function handleSearch(selectedKeys, confirm, dataIndex) {
  confirm()
  searchState = {
    searchText: selectedKeys[0],
    searchedColumn: dataIndex,
  }
}

function handleReset(clearFilters) {
  clearFilters()
  searchState = { searchText: '' }
}

let searchInput = null

function getColumnSearchProps(dataIndex) {
  return {
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            searchInput = node
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
          </Button>
          <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
          </Button>
        </Space>
      </div>
    ),
    filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) =>
      record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput.select())
      }
    },
    render: text => searchState.searchedColumn === dataIndex ? (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[searchState.searchText]}
        autoEscape
        textToHighlight={text.toString()}
      />
    ) : (
      text
    )
  }
}

function App() {
  const state = {
    size: 'small'
  }
  const [dataSource, setDataSource] = useState([])
  const [expandable, setExpandable] = useState({})
  const [dataPrintScreen, setDataPrintScreen] = useState({})
  const [dataHarness, setDataHarness] = useState([])
  const [dataTrappings, setDataTrappings] = useState([])
  const [nameList, setNameList] = useState('')
  const [filters, setFilters] = useState([])
  const [loading, setLoading] = useState(false)
  const [attrDetail] = useState({
    "劲足3级": "坐骑的饱食度大于86%时，移动速度额外提升8%",
    "劲足4级": "坐骑的饱食度大于86%时，移动速度额外提升10%",
    "劲足5级": "坐骑的饱食度大于86%时，移动速度额外提升12%",
    "劲足6级": "坐骑的饱食度大于86%时，移动速度额外提升14%",
    "劲足7级": "坐骑的饱食度大于86%时，移动速度额外提升16%",
    "劲足8级": "坐骑的饱食度大于86%时，移动速度额外提升18%",
    "匹马5级": "在非双人同骑的时候，速度额外提升9%",
    "匹马6级": "在非双人同骑的时候，速度额外提升10%",
    "匹马7级": "在非双人同骑的时候，速度额外提升12%",
  })

  const onDownload = e => {
    const a = document.createElement("a")
    a.download = '九州寻驹图.xlsx'
    a.href = 'https://bj.bcebos.com/v1/wz950116/jx3-pvx/坐骑/public/九州寻驹图.xlsx'
    a.click()
  }

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true)

      // 列表数据
      try {
        const mountsRes = await fetch('/data/mounts/mounts.json')
        const mounts = await mountsRes.json()
        setDataSource(mounts)
      } catch (err) {
        console.log('列表请求失败')
      }

      // 详情数据
      try {
        const dataDescriptionRes = await fetch('/data/mounts/description.json')
        const dataDescription = await dataDescriptionRes.json()
        const expandable = {
          expandedRowRender: record => <p>{dataDescription[record.name] || dataDescription[record.name + '·' + record.suffix]}</p>,
          rowExpandable: record => !!(dataDescription[record.name] || dataDescription[record.name + '·' + record.suffix])
        }
        setExpandable(expandable)
      } catch (err) {
        console.log('详情数据请求失败')
      }

      // 截图
      try {
        const dataPrintScreenRes = await fetch('/data/mounts/printscreen.json')
        const dataPrintScreen = await dataPrintScreenRes.json()
        setDataPrintScreen(dataPrintScreen)
      } catch (err) {
        console.log('截图数据请求失败')
      }

      // 马具
      try {
        const dataHarnessRes = await fetch('/data/mounts/harness.json')
        const dataHarness = await dataHarnessRes.json()
        setDataHarness(dataHarness)
      } catch (err) {
        console.log('马具数据请求失败')
      }

      // 马饰
      try {
        const dataTrappingsRes = await fetch('/data/mounts/trappings.json')
        const dataTrappings = await dataTrappingsRes.json()
        setDataTrappings(dataTrappings)
      } catch (err) {
        console.log('马饰数据请求失败')
      }
      setLoading(false)
    }
    fetchData()
  }, [])

  useEffect(() => {
    // 排序
    if (dataSource.length === 0) return
    const nameList = [], filters = []
    dataSource.forEach(v => {
      if (!nameList.includes(v.name)) {
        nameList.push(v.name)
        filters.push({
          text: v.name,
          value: v.name,
          speed: v.speed.replace('%', '')
        })
      }
    })
    filters.sort((a, b) => {
      return a.speed - b.speed
    })
    setNameList(nameList.join())
    setFilters(filters)
  }, [dataSource])

  const columns = [
    {
      title: '名称',
      dataIndex: 'name',
      filters,
      onFilter: (value, record) => record.name.indexOf(value) === 0
    },
    {
      title: '后缀',
      dataIndex: 'suffix',
      align: 'center'
    },
    {
      title: '移动速度',
      dataIndex: 'speed',
      align: 'center',
      sorter: {
        compare: (a, b) => a.speed.replace('%', '') - b.speed.replace('%', '')
      },
      render: (text, row) => <span style={{ color: row.speed === '120%' ? '#f81d22' : '' }}>{text}</span>
    },
    {
      title: '综合速度',
      dataIndex: 'total_speed',
      align: 'center',
      sorter: {
        compare: (a, b) => a.total_speed.replace('%', '') - b.total_speed.replace('%', '')
      },
      render: (text, row) => <span style={{ color: row.total_speed === '138%' ? '#f81d22' : '' }}>{text}</span>
    },
    {
      title: '属性',
      dataIndex: 'tags',
      render: (text, row, index) => {
        const tags = [row.attrs_1, row.attrs_2, row.attrs_3, row.attrs_4]
        if ((row.attrs_1 && row.attrs_1.includes('劲足')) || (row.attrs_1 && row.attrs_1.includes('匹马'))) {
          const index = tags.indexOf(row.attrs_1)
          swapArr(tags, index, 0)
        }
        if ((row.attrs_2 && row.attrs_2.includes('劲足')) || (row.attrs_2 && row.attrs_2.includes('匹马'))) {
          const index = tags.indexOf(row.attrs_2)
          swapArr(tags, index, 0)
        }
        if ((row.attrs_3 && row.attrs_3.includes('劲足')) || (row.attrs_3 && row.attrs_3.includes('匹马'))) {
          const index = tags.indexOf(row.attrs_3)
          swapArr(tags, index, 0)
        }
        if ((row.attrs_4 && row.attrs_4.includes('劲足')) || (row.attrs_4 && row.attrs_4.includes('匹马'))) {
          const index = tags.indexOf(row.attrs_4)
          swapArr(tags, index, 0)
        }
        if (row.doubleRide === '1') tags.unshift('双骑')
        return (
          <span>
            {tags.map(tag => {
              if (!tag) return null
              let color = tag === '双骑' ? 'geekblue' : tag.includes('劲足') || tag.includes('匹马') ? 'volcano' : 'green'
              return (
                <React.Fragment key={tag}>
                  {
                    attrDetail[tag] ? (
                      <Tooltip placement="top" title={attrDetail[tag]} key={tag}>
                        <Tag color={color} key={tag}>
                          {tag.toUpperCase()}
                        </Tag>
                      </Tooltip>
                    ) : (
                      <Tag color={color} key={tag}>
                        {tag.toUpperCase()}
                      </Tag>
                    )
                  }
                </React.Fragment>
              )
            })}
          </span>
        )
      }
    },
    {
      title: '来源',
      dataIndex: 'origin',
      ...getColumnSearchProps('origin')
    },
    {
      title: '品质等级',
      dataIndex: 'grade',
      align: 'center'
    },
    {
      title: '截图',
      dataIndex: 'printscreen',
      align: 'center',
      render: (text, row, index) => {
        return (
          dataPrintScreen[row.name + '·' + row.suffix] ?
            <a href={dataPrintScreen[row.name + '·' + row.suffix]} target='_blank' rel='noreferrer noopener'>
              <SearchOutlined style={{ fontSize: '14px', color: '#08c', cursor: 'pointer' }} />
            </a> : dataPrintScreen[row.name] ?
              <a href={dataPrintScreen[row.name]} target='_blank' rel='noreferrer noopener'>
                <SearchOutlined style={{ fontSize: '14px', color: '#08c', cursor: 'pointer' }} />
              </a> : ''
        )
      }
    }
  ]

  const columns2 = [
    {
      title: '名称',
      dataIndex: 'name',
      width: 100,
      align: 'center',
      render: (text, row, index) => {
        if (index % 3 === 0) {
          return {
            children: <span>{text}</span>,
            props: {
              rowSpan: 3,
            }
          }
        }
        return {
          children: text,
          props: {
            rowSpan: 0,
            colSpan: 0
          }
        }
      }
    },
    {
      title: '部位',
      dataIndex: 'suffix',
      width: 60,
      align: 'center'
    },
    {
      title: '速度',
      dataIndex: 'speed',
      width: 60,
      align: 'center'
    },
    {
      title: '来源',
      dataIndex: 'origin',
      width: 200,
      align: 'center'
    },
    {
      title: '描述',
      dataIndex: 'description',
      minWidth: 200,
      align: 'left',
      render: (text, row, index) => {
        if (index % 3 === 0) {
          return {
            children: <span>{text}</span>,
            props: {
              rowSpan: 3,
            }
          }
        }
        return {
          children: text,
          props: {
            rowSpan: 0,
            colSpan: 0
          }
        }
      }
    }
  ]

  const columns3 = [
    {
      title: '名称',
      dataIndex: 'name',
      width: 100,
      align: 'center'
    },
    {
      title: '速度',
      dataIndex: 'speed',
      width: 60,
      align: 'center'
    },
    {
      title: '来源',
      dataIndex: 'origin',
      width: 200,
      align: 'center'
    },
    {
      title: '描述',
      dataIndex: 'description',
      minWidth: 200,
      align: 'left'
    }
  ]

  return (
    <React.Fragment>
      {false ? nameList : ''}
      <div className="download">
        <Tooltip title="下载文件">
          <Button type="primary" shape="circle" icon={<DownloadOutlined />} size="large" loading={loading} onClick={onDownload} />
        </Tooltip>
      </div>
      <Row>
        <Col className="gutter-row" span={24}>
          <Table {...state} loading={loading} expandable={expandable} columns={columns} dataSource={dataSource} pagination={false} />
        </Col>
        <Col className="gutter-row" span={12}>
          <Table {...state} loading={loading} columns={columns2} dataSource={dataHarness} pagination={false} bordered />
        </Col>
        <Col className="gutter-row" span={12}>
          <Table {...state} loading={loading} columns={columns3} dataSource={dataTrappings} pagination={false} bordered />
        </Col>
      </Row>
      <BackTop>
        <div className="ant-back-top-content"><div className="ant-back-top-icon"></div></div>
      </BackTop>
    </React.Fragment>
  )
}

export default App
